import React, { Component } from 'react';

export default class VideoContent extends Component {

    state = {
        playImgIdle: "data:image/png;base64,",
        playImgPlay: "data:image/png;base64,",
        firstLoad: false,
        className: "",
        videoId: "",
        width: 0,
        height: 0,
        styleImg: {},
        styleIframe: {},
        thumbnailQuality: "",
        showContent: false
    };

    render() {
        const { src = "", params = {}} = this.props;
        let localState = this.state;

        if (!this.state.firstLoad) {

            const thumbnailQuality = {default: "default", hq: "hqdefault", mq: "mqdefault", sd: "sddefault", maxres: "maxresdefault"};
            let findInUrl = false;
            if (src.indexOf("youtube") !== -1 && src.indexOf("watch") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URLSearchParams(new URL(src).search).get("v");
                } catch {
                    try {
                        localState.videoId = new URLSearchParams(new URL("https://"+src).search).get("v");
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };

            if (src.indexOf("youtube") !== -1 && src.indexOf("embed") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URL(src).pathname.replace('/embed/', '');
                } catch {
                    try {
                        localState.videoId = localState.videoId = new URL("https://"+src).pathname.replace('/embed/', '');
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };

            if (src.indexOf("youtu.be") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URL(src).pathname.replace('/', '');
                } catch {
                    try {
                        localState.videoId = new URL("https://"+src).pathname.replace('/', '');
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };
            
            if (!findInUrl) {
                localState.videoId = src;
            } else {
                if (!this.state.videoId) {
                    localState.firstLoad = true;
                    return(<div>error loading, not found video ID...</div>);
                };
            };

            if (params.hasOwnProperty("height")) {
                localState.height = params.height;
            } else {
                localState.height = 60;
            };
            if (params.hasOwnProperty("width")) {
                localState.width = params.width;
            } else {
                localState.width = 120;
            };
            if (params.hasOwnProperty("title")) {
                localState.title = params.title;
            } else {
                localState.title = "Video from YouTube";
            };
            if (params.hasOwnProperty("className")) {
                localState.className = params.className;
            } else {
                localState.className = "VideoContent__"+this.state.videoId;
            };
            if (params.hasOwnProperty("thumbnailQuality")) {
                localState.height = params.height;
                if (params.thumbnailQuality === "auto") {
                    if (localState.width <= 120) {
                        localState.thumbnailQuality = "default"; 
                    } else if (localState.width <= 320) {
                        localState.thumbnailQuality = "mqdefault"; 
                    } else if (localState.width <= 480) {
                        localState.thumbnailQuality = "hqdefault"; 
                    } else if (localState.width > 480) {
                        localState.thumbnailQuality = "maxresdefault"; 
                    }; 
                } else {
                    try {
                        localState.thumbnailQuality = thumbnailQuality[params.thumbnailQuality];
                    } catch {
                        localState.thumbnailQuality = "maxresdefault";
                    };
                };
            } else {
                if (localState.width <= 120) {
                    localState.thumbnailQuality = "default"; 
                } else if (localState.width <= 320) {
                    localState.thumbnailQuality = "mqdefault"; 
                } else if (localState.width <= 480) {
                    localState.thumbnailQuality = "hqdefault"; 
                } else if (localState.width > 480) {
                    localState.thumbnailQuality = "maxresdefault"; 
                }; 
            };
            localState.styleImg = {
                backgroundPosition: 'center center', 
                backgroundRepeat: 'cover', 
                backgroundImage: "url(https://img.youtube.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg)", 
                backgroundSize: 'cover',
                cursor: "pointer",  
                width: this.state.width-((this.state.width/3)*2)+'px', 
                height: this.state.height-((this.state.height/4)*2)+'px',
                padding: this.state.height/4+'px '+this.state.width/3+'px',
              };
            if (params.hasOwnProperty("styleImg")) {
                localState.styleImg = Object.assign({}, localState.styleImg, params.styleImg);
            };
            localState.styleIframe = {};
            if (params.hasOwnProperty("styleIframe")) {
                localState.styleIframe = Object.assign({}, localState.styleIframe, params.styleIframe);
            };
            if (params.hasOwnProperty("playImgIdle")) {
                localState.playImgIdle = params.playImgIdle;
            };
            if (params.hasOwnProperty("playImgHover")) {
                localState.playImgHover = params.playImgHover;
            };

        };

        localState.firstLoad = true;
        
        if (this.state.showContent) {
            return (
                <iframe className={this.state.className+"__iframe"} key={"vc_"+this.state.videoId+"__iframe"} title={this.state.title} alt={this.state.title} src={"https://www.youtube.com/embed/"+this.state.videoId} style={this.state.styleIframe} allow='autoplay; encrypted-media' width={params.width} height={params.height} frameborder={0} />
            );
        } else {
            return (
                <img className={this.state.className+"__img"} key={"vc_"+this.state.videoId+"__img"} title={this.state.title} alt={this.state.title} src={this.state.playImgIdle} style={this.state.styleImg}
                  onMouseOver={(e) => (e.currentTarget.src = this.state.playImgPlay)}
                  onMouseOut={(e) => (e.currentTarget.src = this.state.playImgIdle)}
                  onClick={() => {this.setState({showContent: true})}} 
                  />
            );
        }

    };
};

